<template>
  <div class="backyard-preference">

    <div class="pedia-navigation">
      <span class="item active"></span>
      <span class="tool">
        <button class="btn btn-sm btn-primary" @click.stop.prevent="systemCleanup" title="">
          <i class="fa fa-warning"></i>
          
        </button>
        <router-link class="btn btn-sm btn-primary" to="/preference/edit">
          <i class="fa fa-pencil"></i>
          
        </router-link>
      </span>
    </div>


    <div class="preference-block">
      <div class="row">
        <div class="col-md-12 form-info">
          <span></span>
          <span class="bold">{{preference.name}}</span>
        </div>

        <div class="col-md-12 form-info">
          <span>logo</span>
          <span>
            <img v-if="preference.logoUrl" :src="preference.logoUrl" alt="logo" class="max-height-100">
          </span>
        </div>

        <div class="col-md-12 form-info">
          <span>favicon</span>
          <span>
            <img v-if="preference.logoUrl" :src="preference.logoUrl" alt="logo" class="max-height-100">
          </span>
        </div>

        <div class="col-md-12 form-info">
          <span></span>
          <span v-html="preference.copyright"></span>
        </div>

        <div class="col-md-12 form-info">
          <span></span>
          <span v-html="preference.record"></span>
        </div>

        <div class="col-md-12 form-info">
          <span>zip</span>
          <span v-html="preference.downloadDirMaxNum"></span>
        </div>

        <div class="col-md-12 form-info">
          <span></span>
          <span>{{preference.allowRegister?'':''}}</span>
        </div>

        <div class="col-md-12 form-info">
          <span>zip</span>
          <span>
            <span class="mr10">
                  :
                    <span v-if="preference.downloadDirMaxSize >= 0">
                            {{preference.downloadDirMaxSize | humanFileSize}}
                          </span>
                    <span v-else>
                            
                          </span>
                </span>
          </span>
        </div>


        <div class="col-md-12 form-info">
          <span></span>
          <span v-html="preference.defaultTotalSizeLimit"></span>
        </div>

      </div>
    </div>

  </div>
</template>

<script>
  import NbSwitcher from "../../common/widget/NbSwitcher"

  export default {
    data() {
      return {
        preference: this.$store.state.preference
      }
    },
    methods: {
      systemCleanup() {

        let that = this
        let preference = this.preference

        this.$prompt('', '', {
          inputValue: null,
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^.{1,45}$/,
          inputErrorMessage: '45'
        }).then(({value}) => {

          preference.httpSystemCleanup(value, function () {
            that.$message.success("");
          })
        }).catch(() => {

        });

      }
    },
    components: {
      NbSwitcher
    },
    mounted() {
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  .backyard-preference {
    .max-height-100 {
      max-height: 100px;
    }

    .preference-block {
      background-color: white;
      box-shadow: 0 0 5px rgba(0, 0, 0, .2);
      border-radius: 5px;
      padding: 20px 15px 10px 15px;
      margin-top: 10px;
      margin-bottom: 30px;
    }
  }
</style>



